import React from 'react';
import { Dialog, DialogTitle, DialogContent, Paper, Table, TableBody, TableRow, TableCell, DialogActions, Button } from '@material-ui/core';
import moment from 'moment';
import { Edit, Close } from '@material-ui/icons';


interface Show{
    product       : any,
    modalShow     : boolean,
    user          : any,



    initModalEdit() : void
    setModalShow(a:boolean) : void,
}

const Show = (props:Show) => {

    if(props.product){
        return(
            <Dialog
                fullWidth
                maxWidth="sm"
                open={props.modalShow}
                onClose={ () => props.setModalShow(false) }
                aria-labelledby="max-width-dialog-title"
            >
                <DialogTitle id="max-width-dialog-title" style={{textAlign:'center'}}>Product Details</DialogTitle>
                <DialogContent>

                    <Paper className="product-modal-table">
                            <Table className="product-modal-list">
                                <TableBody>
                                    <TableRow>
                                        <TableCell align="left" variant="head">Supplier Code</TableCell>
                                        <TableCell align="left">{props.product.supplier.supplier_code}</TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left" variant="head" >Product Code</TableCell>
                                        <TableCell align="left">{props.product.product.product_code}</TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left" variant="head" >Product Name</TableCell>
                                        <TableCell align="left">{props.product.product.product_name}</TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left" variant="head" >Category</TableCell>
                                        <TableCell align="left">{props.product.product.category.name}</TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left" variant="head" >Serial Number</TableCell>
                                        <TableCell align="left">{props.product.serial_number}</TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left" variant="head" >Delivery Date</TableCell>
                                        <TableCell align="left">{moment(props.product.delivery_date).format("LL")}</TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left" variant="head" >Warranty</TableCell>
                                        <TableCell align="left">{props.product.warranty > 1 ? props.product.warranty + ' Years ' : props.product.warranty + " Year"}</TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left" variant="head" >Warranty Start</TableCell>
                                        <TableCell align="left">{moment(props.product.warranty_start).format("LL")}</TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left" variant="head" >End Date</TableCell>
                                        <TableCell align="left">{moment(new Date(props.product.warranty_start).setFullYear(new Date(props.product.warranty_start).getFullYear() + props.product.warranty)).format("LL")}</TableCell>
                                    </TableRow>

                                    <TableRow>
                                        <TableCell align="left" variant="head" >Delivery Doc</TableCell>
                                        <TableCell align="left">{props.product.reference_delivery_document}</TableCell>
                                    </TableRow>

                                    <TableRow>
                                        <TableCell align="left" variant="head" >Created By</TableCell>
                                        <TableCell align="left">{props.product.created_by.first_name + " " + props.product.created_by.last_name}</TableCell>
                                    </TableRow>

                                    <TableRow>
                                        <TableCell align="left" variant="head" >Status</TableCell>
                                        <TableCell align="left">{
                                            props.product.status === 1 ? 'New' :
                                            props.product.status === 2 ? 'Replaced'  : 'Returned'
                                        }</TableCell>
                                    </TableRow>

                                    <TableRow>
                                        <TableCell align="left" variant="head" >Remarks</TableCell>
                                        <TableCell align="left">{props.product.remarks}</TableCell>
                                    </TableRow>

                                </TableBody>
                            </Table>
                    </Paper>

                </DialogContent>
                <DialogActions className="product-modal-footer">
                    {
                        props.user.level !== 3 ?
                            <Button 
                                onClick={props.initModalEdit} 
                                color="primary" 
                                variant="contained"
                            >  
                                <Edit />
                                Edit
                            </Button>
                        : null
                    }
                   
                    <Button onClick={() => props.setModalShow(false)} color="secondary" variant="contained">
                        <Close />   
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
        );
    }else{
        return null;
    }
    
}


export default Show;