import React from 'react';
import {ComponentMiddleware} from 'Middleware';
import {BrowserRouter,Route,Switch, Redirect} from 'react-router-dom'
import * as Component from 'Components';
import {OutsideNav} from './OutsideNav';
import {InsideNav} from './InsideNav';
import { Token, Requests } from 'Services';
import jwt_decode from 'jwt-decode';
import { useDispatch } from 'react-redux';
import { UserAccount as User } from 'Redux/Actions';

const Navigations = (props:any) =>{


    const [ initialized, setInitialized ] = React.useState(false);
    const updateUser = useDispatch();

    React.useEffect(()=>{
        if(!initialized){
            checkToken();
        }
    },[initialized]);


    const checkToken = async () => {
        if(Token.exist()){
            const id:any = jwt_decode(Token.get());
            const user:Requests.Format = await Requests.User.get(id.sub);
            if(user.status === 200){
                updateUser(User(user.data));
            }else{
                Token.remove();
            }
        }

        setInitialized(true);
    }



    if(!initialized){
        return null;
    }


    return(
        <React.Fragment>
            <BrowserRouter>
            
                <Switch>
                   {
                        OutsideNav.map((element,index) => 
                            <Route exact path ={element.path} component={element.component} key={index} />
                        )
                    }
                    
                    
                    <Route path ="/">
                            <Component.Navigation>
                            <Switch>
                                {
                                    InsideNav.map((element,index) => 
                                        <Route exact={element.exact} path ={element.path} render= {(props) => <ComponentMiddleware {...props} component={element.component} />} key={index} />
                                        // <Route exact path ={element.path} component={element.component} key={index} />
                                    )
                                }
                            
                                {/* <Route exact path ="/" render= {(props) => <ComponentMiddleware {...props} component={Component.Home} />}/>
                                <Route exact path ="/products" render= {(props) => <ComponentMiddleware {...props} component={Component.Products} />}/>
                                <Route exact path ="/suppliers" render= {(props) => <ComponentMiddleware {...props} component={Component.Suppliers} />}/>
                                <Route exact path ="/users" render= {(props) => <ComponentMiddleware {...props} component={Component.Users} />}/>
                                <Route exact path ="/reports" render= {(props) => <ComponentMiddleware {...props} component={Component.Reports} />}/>
                                <Route exact path ="/settings" render= {(props) => <ComponentMiddleware {...props} component={Component.Settings} />}/> */}
                                <Route component={Component.NotFound} />
                                </Switch>


                            </Component.Navigation>
                            
                    </Route>
                    
                </Switch>
            </BrowserRouter>
        </React.Fragment>
    );
}

export default Navigations;