import { MasterCodes as MC } from '../Actions/Types';

const initialState:any = {
	data : ''
};

const MasterCodes = (state = initialState, action:any) =>{
	switch (action.type) {
    	case MC:
			return {
				data : action.payload,
			}
    	default:
      		return state;
 	}
}

export default MasterCodes;