import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Paper, Table, TableBody, TableRow, TableCell, TableFooter, TablePagination, TableHead, Button, Dialog, DialogContent, CircularProgress } from '@material-ui/core';
import TablePaginationActions from '@material-ui/core/TablePagination/TablePaginationActions';
import { Products as productRedux, Suppliers as supplierRedux,Filter as Filterer,NavigationTitle,MasterCodes} from 'Redux/Actions';
import { withRouter } from 'react-router-dom';
import Skeleton from '@material-ui/lab/Skeleton';
import './ProductsStyle.scss';
import { Close, CloudUpload } from '@material-ui/icons';
import moment from 'moment';
import { Requests } from '../../../Services';
import { useSnackbar } from 'notistack';
import Filter from './Filter';
import Add from './Add';
import Show from './Show';
import Edit from './Edit';
import Upload from './Upload';

const Products = (props:any) =>{

    const initErrroMessage = {
        id                          : {error:false,message:''},
        supplier                    : {error:false,message:''},
        product                     : {error:false,message:''},
        // product_description         : {error:false,message:''},
        delivery_date               : {error:false,message:''},
        reference_delivery_document : {error:false,message:''},
        serial_number               : {error:false,message:''},
        warranty                    : {error:false,message:''},
        warranty_start              : {error:false,message:''},
        status                      : {error:false,message:''},
        remarks                     : {error:false,message:''},
    };
    
    const initProductInput = {
        id                  : '',
        supplier            : '',
        product             : '',
        // product_description : '', 
        delivery_date       : '',
        reference_delivery_document : '',
        serial_number       : '',
        warranty            : '',
        warranty_start      : '',
        status              : '',
        remarks             : '',
    
    }

    
    // const [ page, setPage ] = React.useState(0);
    const [ params, setParams ] = React.useState({
        page     : 1,
        per_page : 10
    });
    const [ modalEdit, setModalEdit ] = React.useState(false);  // for showing edit modal
    const [ modalShow, setModalShow ] = React.useState(false);
    const [ modalAdd, setModalAdd ] = React.useState(false);    
    const [ product, setProduct ] = React.useState();   // for holding product info by id
    const [ submit, setSubmit ] = React.useState(false);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar(); //snackbar
    const [ upload,setUpload ] = React.useState({ 
        uploading   : false,
        open       : false,
        result    : [], 
    });
    const [filter, setFilter] = React.useState({
        supplier            : {filter:'iet',key:''},
        product             : {filter:'iet',key:''},
        product_description : {filter:'iet',key:''},
        serial_number       : {filter:'iet',key:''},
        warranty            : {filter:'iet',key:''},
        warranty_start      : {filter:'iet',key:''},
        warranty_end        : {filter:'iet',key:''},
        status              : {filter:'iet',key:''}
    });
    const [ productCodeParam, setProductCodeParam ] = React.useState();



    // const params = {page : 1,per_page : 10};
    let file:HTMLInputElement;

    const [ productInput, setProductInput ] = React.useState(initProductInput);

    const [responseMessage, setResponseMessage] = React.useState(initErrroMessage);

    const productState = useSelector( (state:any) => state.Products.data );
    const supplierState =  useSelector ( (state:any) => state.Suppliers.data );
    const user  = useSelector( (state:any) => state.UserAccount.data);

    const dispatch = useDispatch();


    const getMasterCode = async () => {
		const a = await Requests.MasterCodes.show({page:1,per_page:10});

		let meta = a.data.meta;

		let b;

		if(meta.per_page < meta.total){
            b = await Requests.MasterCodes.show({page:1,per_page:meta.total});
            setProductCodeParam({
                page : 1,
                per_page : meta.total
            })    
		}else{
            b = a;
            setProductCodeParam({
                page : 1,
                per_page : 10
            });    
		}

        
		dispatch(MasterCodes(b));

	}


    useEffect(()=>{
        getMasterCode();

        dispatch(NavigationTitle('Products'));        
        dispatch(supplierRedux({page : 1, per_page : 10000}));  //for select option
        dispatch(Filterer(filter,"product",params));
        
        return () =>{
            dispatch(NavigationTitle(''));
        }
    },[props]);

    
    const actions = (key:any) => (
        <Button onClick={() => { closeSnackbar(key) }}>
            <div style={{color:"white"}}><Close /></div>
        </Button>
    );
    
    
    const resetError = () =>{
        setResponseMessage(initErrroMessage);
    };
    
    // const productInterval = setInterval( async () =>{
    //     if(productState !== '' && productState.status!== 200){
    //         props.history.push('/login');
    //     }
    //     products(productRedux(params));
    // },30000);
    
    const handleChangePage = (event: React.MouseEvent<HTMLButtonElement, MouseEvent> | null,newPage: number,) =>{
        let pams:any = params;
        params.page=newPage+1;
        setParams(pams);

        console.log(newPage);

        dispatch(Filterer(filter,"product",pams));
    }

    const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
        const val:any = event.target.value;
        let paran = {page:1,per_page:val};

        setParams(paran);
        dispatch(Filterer(filter,"product",paran));
        
    };

    const uploadFile = () => {
        togglePointerEvents(false);
        if(file.files.length !== 0){
            const selectedFile = file.files[0];
            let filename = selectedFile.name.split('.').pop();
            
            if( !(filename === 'xls' || filename === 'xlsx')) {
                enqueueSnackbar('Please select xlsx or xls file only !!!. ',{variant:'error',action:actions});
                togglePointerEvents(true);
                return;
            }else{
                uploadSheet(selectedFile);
            }
        }
    }

    const uploadSheet = async (file:File) => {

        
        setUpload({
            ...upload,
            uploading : true,
        });

        let response = await Requests.Products.addFile(file);

        switch(response.status){
            case 406 :
                enqueueSnackbar("Spreadsheet format is invalid",{variant:'error',action:actions});
                togglePointerEvents(true);
                setUpload({
                    ...upload,
                    uploading : false,
                });
                return;
            case 200 :
                
                enqueueSnackbar('Sheet successfully imported!!',{variant:'success',action:actions});

                togglePointerEvents(true);
                dispatch(Filterer(filter,"product",params));
                setUpload({
                    open      : true,
                    uploading : false,
                    result    : response.data
                });
                return;

            default :
                enqueueSnackbar('Something went wrong. Please try again',{variant:'error',action:actions});
                togglePointerEvents(true);

                setUpload({
                    ...upload,
                    uploading : false,
                });
                return;
        }
    }

    const uploadFileModal = () =>{
        if(upload.uploading){
            return (
                <Dialog
                    maxWidth="xs"
                    open={upload.uploading}
                    disableBackdropClick
                    className="uploading-processing"
                >
                    <DialogContent>
                        <div>
                            <div className="loading-submit-upload" style={{textAlign:"center"}}>
                                <CircularProgress />
                            </div>
                            <div className="status-submit-upload">
                                Uploading
                            </div>
                        </div>
                    </DialogContent>
                </Dialog>
            );
        }else{
            return null;
        }
    }


    const skeletonTable = () => {

        let a:any = [];

        const tableCell = <TableCell align="right"><Skeleton variant="rect" width={118} height={20} /></TableCell>;
        for(let i = 0;i < 10;i++ ){
            a.push(
                <TableRow key={i}>
                    {tableCell}
                    {tableCell}
                    {tableCell}
                    {tableCell}
                    {tableCell}
                    {tableCell}
                    {tableCell}
                    {tableCell}
                </TableRow>
            );
        }

        return a;
    }

    const togglePointerEvents = (event:boolean) => {
        if(event){
            document.getElementsByTagName("html")[0].style.pointerEvents = "unset";
            document.getElementsByTagName("html")[0].style.cursor = "unset";
        }else{
            document.getElementsByTagName("html")[0].style.pointerEvents = "none";
            document.getElementsByTagName("html")[0].style.cursor = "progress";
        }
        return;
    }

    const initModalAdd = () => {
        
        const today  = new Date();
        setProductInput({
            ...initProductInput,
            delivery_date       : moment(today).format('MM/DD/YYYY'),
            warranty_start     : moment(today).format('MM/DD/YYYY'),
        });

        // console.log(productInput);
        // setResponseMessage(initErrroMessage);
        resetError();
        setModalAdd(true);
    }

    const initModalShow = (productInfo:any) =>{
        setProduct(productInfo);

        console.log(productInfo);

        setModalShow(true);
    }

    const updateInput = (event:any, value?:any) => {

        if(event){
            if(event instanceof Date){
                setProductInput({
                    ...productInput,
                    [value] : moment(event).format('MM/DD/YYYY'),
                })
            }else{
                setProductInput({
                    ...productInput,
                    [event.target.name] : event.target.value,
                })
            }
        }
    }


    const initModalEdit = () =>{

        resetError();
        // setResponseMessage(initErrroMessage);    // reset error message state before mounting modal;

        setProductInput({
            id                          : product.id,
            supplier                    : product.supplier.id,
            product                     : product.product.id,
            // product_description         : product.product_description,
            delivery_date               : product.delivery_date,
            reference_delivery_document : product.reference_delivery_document,
            serial_number               : product.serial_number,
            warranty                    : product.warranty,
            warranty_start              : product.warranty_start,
            status                      : product.status,
            remarks                     : product.remarks,
        });

        setModalShow(false);
        setModalEdit(true);
    }


    type action = "edit" | "add-file" | "add";
    const submitForm = async (event:any, action:action) => {

        resetError();

        
        // setResponseMessage(initErrroMessage);    // reset error message state before submiting;

        event.persist();  //to access the event properties in an asynchronous way. https://reactjs.org/docs/events.html#event-pooling
        event.preventDefault();
        togglePointerEvents(false);
        setSubmit(true);

        switch(action){
            case "edit" :
                await Requests.Products.update(productInput).then( 
                    (response:any) =>{
                        if(response.network_error){
                            enqueueSnackbar('Network error, please contact administrator!!!',{variant:'error',action:actions});
                            setModalEdit(false);
                        }else{
                            if(response.status === 200){
                                enqueueSnackbar('Product successfully updated!!!',{variant:'success',action:actions});
                                setModalEdit(false);
                                dispatch(productRedux(params));
                            }
                            else{
                                enqueueSnackbar('Update failed',{variant:'error',action:actions});
                                updateErrorState(response.data.errors);
                            }
                        }
                    }
                ).catch(()=>{
                    enqueueSnackbar('Something went wrong. Please try again!!!',{variant:'error',action:actions});
                    setModalEdit(false);
                });
                break;
            case "add" :
                await Requests.Products.add(productInput).then( 
                    (response:any) =>{
                        if(response.network_error){
                            enqueueSnackbar('Network error, please contact administrator!!!',{variant:'error',action:actions});
                            setModalAdd(false);
                        }else{
                            if(response.status === 200){
                                enqueueSnackbar('Product successfully added!!!',{variant:'success',action:actions});
                                setModalAdd(false);
                                dispatch(productRedux(params));
                            }
                            else{
                                enqueueSnackbar('Add new product failed!!',{variant:'error',action:actions});
                                updateErrorState(response.data.errors);
                            }
                        }
                    }
                ).catch((e:any)=>{
                    enqueueSnackbar('Something went wrong. Please try again!!!. '+ e ,{variant:'error',action:actions});
                    setModalAdd(false);
                });
                break;
        }
        
        setSubmit(false);
        togglePointerEvents(true);
        return;
    }

    const updateErrorState = (response:[]) => {
        let holder:any = initErrroMessage;
        response.forEach( (value:any) =>{
            holder[value.name] = {error : true,message: value.message};
        });
        
        setResponseMessage(holder);
    }

    const status = (stat:any) => {
        switch(stat){
            case 1 : 
                return "New";
            case 2 :
                return "Replace";
            case 3 : 
                return "Returned";
        }
    }


    return(
        <React.Fragment>
            <Edit
                supplierState   = {supplierState}
                modalEdit       = {modalEdit}
                updateInput     = {updateInput}
                submit          = {submit}
                responseMessage = {responseMessage}
                productInput    = {productInput}
                product         = {product}
                submitForm      = {submitForm}
                setModalEdit    = {setModalEdit}
                params          = {productCodeParam}
            />
            <Add 
                modalAdd        = {modalAdd}
                setModalAdd     = {setModalAdd}
                updateInput     = {updateInput}
                productInput    = {productInput}
                submit          = {submit}
                responseMessage = {responseMessage}
                supplierState   = {supplierState}
                submitForm      = {submitForm}
                setProductInput = {setProductInput}
                params          = {productCodeParam}
            />
            <Show 
                product       = {product}
                modalShow     = {modalShow}
                user          = {user}
                initModalEdit = {initModalEdit}
                setModalShow  = {setModalShow}
            />
            <Upload
                upload      = {upload}
                setUpload   = {setUpload}
            />
            {uploadFileModal()}
            <Paper className="productPaperTable">
                <Table className="product-list" size="small">
                    <TableHead>
                        <TableRow>
                            <TableCell align="center">Supplier Code</TableCell>
                            <TableCell align="center">Product Code</TableCell>
                            <TableCell align="center">Product Name</TableCell>
                            <TableCell align="center">Serial Number</TableCell>
                            <TableCell align="center">Warranty</TableCell>
                            <TableCell align="center">Warranty Start</TableCell>
                            <TableCell align="center">End Date</TableCell>
                            <TableCell align="center">Status</TableCell>                                     
                        </TableRow>
                    </TableHead>
                    
                    <TableBody>
                        <Filter filter={filter} setFilter={setFilter} params={params}/>

                        {
                            productState ?
                                productState.data.data.map((key:any,id:number)=>(
                                    <TableRow key={id}  hover={true} style={{cursor:'pointer'}} onDoubleClick={() => initModalShow(key)} >
                                        <TableCell align="left" title={key.supplier.supplier_code}>{key.supplier.supplier_code}</TableCell>
                                        <TableCell align="left" title={key.product.product_code}>{key.product.product_code}</TableCell>
                                        <TableCell align="left" title={key.product.product_name}>{key.product.product_name}</TableCell>
                                        <TableCell align="left" title={key.serial_number}>{key.serial_number}</TableCell>
                                        <TableCell align="center" title={key.warranty}>{key.warranty}</TableCell>
                                        <TableCell align="left" title={moment(new Date(key.warranty_start)).format("ll")}>{moment(new Date(key.warranty_start)).format("ll")}</TableCell>
                                        <TableCell align="left" title={moment(new Date(key.warranty_end)).format("ll")}>{moment(new Date(key.warranty_end)).format("ll")}</TableCell>
                                        <TableCell align="center" title={status(key.status)}>{status(key.status)}</TableCell>
                                    </TableRow>
                                ))
                            :   skeletonTable()
                        }
                    </TableBody>
                    <TableFooter>
                        <TableRow className="product-footer-table">
                            <td>
                                { user.level !== 3 ?
                                    <React.Fragment>
                                        <Button onClick={ () =>initModalAdd()} variant="contained" color="primary" className="product-add-new">
                                            ADD NEW
                                        </Button>

                                    
                                        <input type="file" hidden name="xlsx_upload" accept=".xlsx" ref={input => file=input } onChange={uploadFile}/>
                                        <Button 
                                            color="primary"
                                            variant="contained"
                                            onClick={(event)=>{
                                                event.persist();
                                                file.click()
                                            }}
                                            className = "product-add-file"
                                        >
                                            <CloudUpload />&nbsp;  
                                            Import file
                                        </Button>
                                    </React.Fragment>
                                    : ''
                                }
                            </td>
                            <td>
                                
                            </td>

                            <TablePagination
                                rowsPerPageOptions={[10,25,50,100]}
                                colSpan={0}
                                count={productState ? productState.data.meta.total : 10}
                                rowsPerPage={params.per_page}
                                page={productState ? params.page-1: 0}
                                onChangePage={handleChangePage}
                                onChangeRowsPerPage={(handleChangeRowsPerPage)}
                                ActionsComponent={TablePaginationActions}
                                className="product-pagination"
                            />
                        </TableRow>
                    </TableFooter>
                </Table>
            </Paper>
        </React.Fragment>
    );
}

export default withRouter(Products);