interface showMasterCodes{
    page : number,
    per_page : number,
}




class MasterCodes extends StaticMethods{

    static async show(user:showMasterCodes){

        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }
        
        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }
        
        const header = super.header(Token.get());
        
        await axios({
            method  :   "GET",
            url     :   Url.productMasterList+'?page='+user.page+'&per_page='+user.per_page,
            headers :   header,
        })
        // .then( this.sleeper(1000))
        .then( (response:any) => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await super.recall({
                error : error,
                callback : this.show,
                format : format,
                params : user
            });
        })
        return format;
    }

    static async get(id:number){

        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }

        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }

        const header = this.header(Token.get());

        await axios({
            method  :   "GET",
            url     :   Url.productMasterList+id,
            headers :   header,
        }).then( response => {
            format.status = response.status;
            format.data = response.data.data;
        }).catch( async (error) =>{
            format = await super.recall({
                error : error,
                callback : this.get,
                format : format,
                params : id
            });
        })
        return format;

    }

    static async update(up:any){
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }

        const header = super.header(Token.get());
        
        await axios({
            method  :   "PATCH",
            url     :   Url.productMasterList+up.id,
            headers :   header,
            params  :   up,
        }).then( (response:any) => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await super.recall({
                error : error,
                callback : this.update,
                format : format,
                params : up
            });
        })
        return format;
    }

    // static async patch(user:Update){

    // }

    static async add(pml:any){
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }
        
        
        const header = super.header(Token.get());
        
        await axios({
            method  :   "POST",
            url     :   Url.productMasterList,
            headers :   header,
            params  :   pml,
        })
        // .then( this.sleeper(1000))
        .then( (response:any) => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await super.recall({
                error : error,
                callback : this.add,
                format : format,
                params : pml
            });
        })
        return format;
    }

    static async addFile(file:File){
        
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }

        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }
        
        const token = Token.get();
        const formData = new FormData();
        formData.append('file',file);

        await axios.post(Url.productMasterList,formData,{
            headers: {
                'Content-Type'         :    'application/x-www-form-urlencoded',
                'Accept'               :    'application/json',
                'Authorization'        :    'Bearer '+token
            }})
        .then( (response:any) => {
            format.status = response.status; 
            format.data = response.data;
        }).catch( async (error) =>{
            format = await super.recall({
                error : error,
                callback : this.addFile,
                format : format,
                params : file
            });
          
        })
        return format;
    }

    static async filter(filter:any){

        let params = new URLSearchParams(filter).toString();

        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }
        
        const header = this.header(Token.get());
        
        await axios({
            method  :   "GET",
            url     :   Url.productMasterList+'?filter=true&'+params,
            headers :   header,
        }).then( this.sleeper(1000))
        .then( (response:any) => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await super.recall({
                error : error,
                callback : this.filter,
                format : format,
                params : filter
            });
        })
        return format;
    }

    // static async delete(id:number){

    // }

}

export default MasterCodes;