    static async add(pml:add){
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }
        
        
        const header = super.header(Token.get());
        
        await axios({
            method  :   "POST",
            url     :   Url.category,
            headers :   header,
            params  :   pml,
        })
        // .then( this.sleeper(1000))
        .then( (response:any) => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await super.recall({
                error : error,
                callback : this.add,
                format : format,
                params : pml
            });
        })
        return format;
    }

