import React from 'react';
import { Button, Dialog, DialogTitle, DialogContent, DialogContentText, FormControl, InputLabel, MenuItem, FormControlLabel, DialogActions, TextField, makeStyles, Theme, createStyles, Fab } from '@material-ui/core';
import { Requests } from 'Services';
import Select from 'react-select';
import AddIcon from '@material-ui/icons/Add';
import Close from '@material-ui/icons/Close';
import { useSnackbar } from 'notistack';
import {MasterCodes} from 'Redux/Actions'
import {useDispatch} from 'react-redux';
import Category from './Category';

interface Add {
    open : boolean,
    handleClose : any,

    per_page : number,
    page : number,
}


const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        container: {
            display: 'flex',
            flexWrap: 'wrap',
        },
        textField: {
            marginLeft: 0,
            marginRight: 0,
            width : '100%',
            marginBottom: '10px',
            marginTop:'0'
            
        },
        dense: {
            marginTop: theme.spacing(0),
        },
        menu: {
            width: 200,
        },
        dialog : {
            overflow: 'visible'
        },
        select : {
            background : 'white',
            position : 'absolute',
            color : '#757575',
            marginTop : '-10px',
            left : '14px',
            zIndex : 1,
            // marginLeft : '40px',
            // fontSize : '12px', 
        },
        category : {
            width : '85%'
        },
        addCategory : {
           marginTop : '-46px',
           right : '15px',
           position : 'absolute',
           transform : 'scale(0.7)',
           outline:'none',

            "&:focus": {
                outline: 'none'
            }
        },
        categoryError : {
            fontSize: '0.75rem',
            color   : '#f11111',
            position : 'absolute',
        }
    }),
);


const Add = (props:Add) => {

    const initError = {
        product_code : {error: false, message : ''},
        product_name : {error: false, message : ''},
        category     : {error: false, message : ''}
    }

    const classes = useStyles(props);

    const dispatch = useDispatch();
    const { enqueueSnackbar, closeSnackbar } = useSnackbar(); //snackbar
    const [ category, setCategory ] = React.useState();
    const [ option, setOption ] = React.useState();
    const [input, setInput] = React.useState({
        product_code : '',
        product_name : '',
        category     : '',
    });
    const [submit, setSubmit ] = React.useState(false);
    const [error, setError] = React.useState(initError);
    const [ categoryHandle, setCategoryHandler ] = React.useState(false);


    const categoryParams = {
        per_page : 10,
        page : 1,
    }


    React.useEffect( () => {
        if(props.open){
            queryCategory();
        }
    },[props]);

    const actions = (key:any) => (
        <Button onClick={() => { closeSnackbar(key) }}>
            <div style={{color:"white"}}><Close /></div>
        </Button>
    );


    const queryCategory = async () => {
        const a = await Requests.Category.show(categoryParams);

        let meta = a.data.meta;

        let opti:any;

        if(meta.total > meta.per_page){
            let b =  await Requests.Category.show({per_page:meta.total,page:1});
            setCategory(b.data.data);
            opti = b.data.data;
            categoryParams.page = 1;
            categoryParams.per_page = meta.total; 
        }else{
            setCategory(a.data.data);
            opti = a.data.data;
        }



        let opt = opti.map((value:any) => {
            return {
                label : value.name,
                value : value.id,
                target: {
                    name  : 'category',
                    value : value.id 
                }
            }
        })

        setOption(opt);
    }

    const handleChangeText = (event:any) => {
        setInput({
            ...input,
            [event.target.name] : event.target.value
        });
    }

    // const reset = () => {

    //     // let a = {
            
    //     // }


        

    //     console.log(error);
    // }


    // const checkNull = () => {
    //     if(input.product_code === ''){
    //         setError({
    //             ...error,
    //             product_code : {error: true, message : 'Product Code is required'}
    //         });
    //         setSubmit(false);
    //         return true;
    //     }

    //     if(input.product_name === ''){
    //         setError({
    //             ...error,
    //             product_name : {error: true, message : 'Product Name is required'}
    //         });
    //         setSubmit(false);
    //         return true;
    //     }

    //     if(input.category === ''){
    //         setError({
    //             ...error,
    //             category : {error: true, message : 'Please select category'}
    //         });
    //         setSubmit(false);
    //         return true;
    //     }

    //     setSubmit(false);
    //     return false;
    // }





    const submitForm = async (event:any) => {
              
        setError(initError); 
        setSubmit(true);  
        event.preventDefault();
        

        // if(!checkNull()){
        //     return;
        // }

        let a = await Requests.MasterCodes.add(input);


        if(a.network_error){
            enqueueSnackbar("Something went wrong. Plese try again later.",{variant:'error',action:actions});
        }else{
            if(a.status === 200){
                enqueueSnackbar("Successfully added Product Code",{variant:"success",action:actions});

                let a = await Requests.MasterCodes.show({per_page:props.per_page,page:props.page});

                if(a.status === 200 ){
                    dispatch(MasterCodes(a));
                }

                
                props.handleClose();
            }else{

                if(a.status === 406){
                    updateErrorState(a.data.errors);
                }
                enqueueSnackbar("Add new product code failed",{variant:'error',action:actions});
            }
        }

        setSubmit(false);
        return;
    }


    const updateErrorState = (response:[]) => {
        let holder:any = initError;
        response.forEach( (value:any) =>{
            holder[value.name] = {error : true,message: value.message};
        });
        setError(holder);
    }

    
    return (
        <React.Fragment>
            <Category
                params = {categoryParams}
                open = {categoryHandle}
                setOpen = {setCategoryHandler}
                setOption = {setOption}
            />
            <Dialog
                disableBackdropClick
                maxWidth="xs"
                open={props.open}
                onClose={props.handleClose}
                aria-labelledby="max-width-dialog-title"
                classes = {{ paperScrollPaper: classes.dialog }}
            >
                <DialogTitle id="max-width-dialog-title">Add New Product</DialogTitle>
                <DialogContent className={classes.dialog}>
                    <form onSubmit={submitForm} id="master-file">
                        
                        <TextField
                            id="outlined-email-input"
                            label="Product Code"
                            className={classes.textField}
                            type="text"
                            name="product_code"
                            value = {input.product_code}
                            margin="normal"
                            variant="outlined"
                            InputLabelProps={{
                                shrink: true,
                            }}
                            onChange = {handleChangeText}
                            error = {error.product_code.error}
                            helperText = {error.product_code.error ? error.product_code.message : ''}
                            disabled={submit}
                        />
                        
                        <TextField
                            id="outlined-email-input"
                            label="Product Name"
                            className={classes.textField}
                            type="text"
                            name="product_name"
                            value = {input.product_name}
                            margin="normal"
                            variant="outlined"
                            InputLabelProps={{
                                shrink: true,
                            }}
                            onChange = {handleChangeText}
                            error = {error.product_name.error  ? true : false}
                            helperText = {error.product_name.message}
                            disabled={submit}
                        />

                        <div style={{position:'relative'}}>
                            <label className={classes.select}>Category</label>
                            <Select
                                options={option}
                                // isClearable
                                name = 'category'
                                onChange = {handleChangeText}
                                
                                // defaultValue = {options[0]}
                                // onChange = {props.updateInput}
                                // isDisabled = {props.submit}
                                className={/*error.category.error? */classes.category/* + "select-error" : classes.category */}
                                required
                                disabled={submit}
                            />
                            <div hidden={!error.category.error} /*style = {styles.select}*/ className={classes.categoryError}>{error.category.message}</div>
                        </div>

                        <Fab color="primary" aria-label="add" className={classes.addCategory} onClick = { () => setCategoryHandler(true) }>
                            <AddIcon />
                        </Fab>
                    </form>
                </DialogContent>
                <DialogActions>
                    <Button disabled={submit} onClick={props.handleClose} color="secondary" variant="contained">
                        Close
                    </Button>

                    <Button form="id" disabled={submit} onClick={submitForm} color="primary" variant="contained">
                        Submit
                    </Button>
                </DialogActions>
          </Dialog>
        </React.Fragment>
      );
}

export default Add;