import { Suppliers as sp, Products as pr, Users as us, MasterCodes as ma } from './Types';
import { Requests } from '../../Services';

type component = "supplier" | "product" | "user" | "master";

interface params {
    page        : number,
    per_page    : number
}

export const Filter = (filter:any,component:component,extra_params:params) => {

        let params = '';
        Object.keys(filter).forEach( (a:any) => {
            if(filter[a].key){
                if(params === ''){
                    params = a+"[filter]="+filter[a].filter+"&"+a+"[key]="+filter[a].key;
                }else{
                    params = params+"&"+a+"[filter]="+filter[a].filter+"&"+a+"[key]="+filter[a].key;
                }
            }
        });

        params = params+"&page="+extra_params.page+"&per_page="+extra_params.per_page;
        

        switch(component){
            case "supplier" :
                return async (dispatch:any) => {

                    dispatch({
                        type    : sp,
                        payload : '',
                    });
        
    
                    dispatch({
                        type    : sp,
                        payload : await Requests.Suppliers.filter(params),
                    });
                }
            case "product" :
                return async (dispatch:any) => {

                    dispatch({
                        type    : pr,
                        payload : '',
                    });
        
    
                    dispatch({
                        type    : pr,
                        payload : await Requests.Products.filter(params),
                    });
                }
            case "user" :
                return async (dispatch:any) => {

                    dispatch({
                        type    : us,
                        payload : '',
                    });
        
    
                    dispatch({
                        type    : us,
                        payload : await Requests.User.filter(params),
                    });
                }
            case "master" :
                return async (dispatch:any) => {

                    dispatch({
                        type    : ma,
                        payload : '',
                    });
        
    
                    dispatch({
                        type    : ma,
                        payload : await Requests.MasterCodes.filter(params),
                    });
                }
        }

        
}
